@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400|Raleway:400,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

html, body {
  height: 100%;
  width: 100%;
}

.App {
  height: 100%;
  justify-content: center;
  background-color: rgba(214,214,214,0.25)
}

/* * {
  margin: 0 !important;
  padding: 0 !important;
} */

@media screen and (min-width: 376px) {
  .wrapper {
    width: 900px !important;
    height: 600px !important;
    position: relative;
    top: 150px;
    /* left: 90px; */
    box-shadow: 0 0 1px 1px rgba(214,214,214,0.5);
    background-color: white;
    display: block;
  }

  .respDesc {
    height: 60%;
    /* width: 380px; */
  }

  .respSignUp {
    height: 40%;
    /* width: 380px; */
  }

  .block1 {
    width: 500px;
  }
}

@media screen and (max-width: 375px) {
  .wrapper {
    background-color: white;
  }

  .block1 {
    margin-left: auto;
    margin-right: auto;
  }
}

.header {
  background-image: url(/fylo-p1/static/media/bg-intro.6d87f674.svg);
  height: 445px;
  width: 375px;
  margin-right: 0;
}

.illustration {
  height: 240px;
  display: block;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  top: 90px;
}

.logo {
  /* text-decoration-color: white; */
  width: 90px;
  /* right: -40px;
  height: 80px; */
}

.contact img {
  color: black;
  fill: currentColor;
}

.contact {
  /* justify-content: center; */
  position: relative;
  top: 40px;
  padding-left: 40px;
  width: 375px;
}

.header a {
  color: white;
  font-size: 14px;
  padding-right: 10px;
  padding-left: 10px;
}

.header .icons {
  padding-left: 45px;
}


@media screen and (min-width: 376px) {
  .header {
    height: 600px;
    width: 400px;
    background-image: -webkit-gradient(linear, left bottom, right top, from(hsl(243, 86%, 85%)), to(hsl(243, 87%, 12%)));
    background-image: -webkit-linear-gradient(left bottom, hsl(243, 86%, 85%), hsl(243, 87%, 12%));
    background-image: linear-gradient(to right top, hsl(243, 86%, 85%), hsl(243, 87%, 12%));
    /* z-index: -1; */
  }

  .illustration {
    top: 160px;
    height: 260px;
  }

  .contact {
    position: absolute;
    /* z-index: 1; */
    left: 520px;
  }

  .logo {
    position: relative;
    right: 110px;
  }

  .header a {
    color: black;
  }
}

.descContainer {
  display: block;
}

/* Typography */

.descColH {
  font-family: 'Raleway';
  color: hsl(243, 87%, 12%);
}

.descColp p {
  font-family: 'Open Sans';
  font-weight: 400;
  font-size: 12px;
  color: hsl(0, 0%, 50%);
  padding-top: 5px;
}

@media screen and (min-width: 376px) {
  .respDesc {
    height: 60%;
    /* z-index: -1; */
    position: relative;
    top: 200px;
  }

  .descContainer {
    width: 400px !important;
  }

  /* .descContainer {
    margin-left: 0 !important;
    margin-right: 0 !important;
  } */

  .descColH h4 {
    width: 400px;
  }

  .descColp p {
    width: 360px;
  }
}

.signUpGrp input {
  width: 265px;
  border-radius: 5px;
  border-style: ridge;
  border-color: rgba(214,214,214,0.5);
  box-shadow: 0px 0px 1px 1px rgba(214,214,214,0.75)
}

.pp {
  color: hsl(243, 87%, 12%);
  text-decoration: underline;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 13px;
  padding-bottom: 5px;
}

.colorMe1 {
  background-color: hsl(163, 95%, 43%) !important;
  border-color: hsl(163, 95%, 43%) !important;
  width: 130px;
}

.callToA {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: hsl(243, 87%, 12%);
  font-size: 15px;
}

@media screen and (min-width: 376px) {
  .signUpGrp {
    height: 40%;
    width: 400px !important;
  }

  .signUpGrp input {
    width: 220px;
    height: 38px;
  }

  .signUpGrp .pp {
    text-align: center;
    position: relative;
    top: 45px;
  }
}

